import React from "react";
import Layout from "../components/Layout";
import HeroPage from "../components/HeroPage";
import TextBox from "../components/TextBox";
import { graphql, useStaticQuery } from "gatsby";
import { makeStyles, useTheme } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
	address: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		flexDirection: "column",
		backgroundColor: "#FFF",
		width: "100%",
		paddingTop: "1rem",
		paddingBottom: "1rem",
		"@media (min-width:650px)": {
			maxWidth: "33.3vw",
		},
	},

	galleryContainer: {
		display: "flex",
		flexDirection: "column",
		"@media (min-width:650px)": {
			flexDirection: "row",
			justifyContent: "space-evenly",
			paddingBottom: "4rem",
		},
		backgroundColor: "white",
	},

	gallery: {
		"@media (min-width:650px)": {
			width: "77.7vw",
		},
	},
	title: {
		width: "100%",
		margin: "auto",
		backgroundColor: "#FFF",
		textAlign: "center",
		paddingTop: "1rem",
		paddingBottom: "1rem",
	},
}));

export default function TestPage() {
	const classes = useStyles();
	const data = useStaticQuery(graphql`
		query {
			image1: file(relativePath: { eq: "images/openWaterDiver.png" }) {
				childImageSharp {
					fluid(maxWidth: 1400) {
						...GatsbyImageSharpFluid
					}
				}
			}
			image2: file(relativePath: { eq: "images/certifiedToday.jpeg" }) {
				childImageSharp {
					fluid(maxWidth: 1400) {
						...GatsbyImageSharpFluid
					}
				}
			}
			image3: file(relativePath: { eq: "images/bg-wave.png" }) {
				childImageSharp {
					fluid(maxWidth: 1400) {
						...GatsbyImageSharpFluid
					}
				}
			}
		}
	`);

	return (
		<HeroPage
			title="Test Page"
			image1={data.image1.childImageSharp.fluid}
			image2={data.image2.childImageSharp.fluid}
			image3={data.image3.childImageSharp.fluid}
		></HeroPage>
	);
}
